import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'))
const School = React.lazy(() => import('./views/dashboard/school/School.js'))
const Student = React.lazy(() => import('./views/dashboard/student/Student.js'))
const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', element: Dashboard },
  { path: '/school', name: 'School', element: School },
  { path: '/student', name: 'Student', element: Student },
]

export default routes
