import React, {useState} from 'react'
import { AppContent, AppSidebar, AppHeader } from '../components/index'

const DefaultLayout = () => {
  const [schoolArr, setSchoolArr] = useState([])
  const [studentArr, setStudentArr] = useState([])
  const [location, setLocation] = useState("home")
  const updateLocation = (event)=>{
    if(event.target.id==="home")
    {
      setLocation("home")
    }
    else if(event.target.id==="school")
    {
      setLocation("school")
    }
    else {
      setLocation("student")
    } 
  }

  const updateSchoolArr = (obj)=>{
    setSchoolArr([...schoolArr,obj])
  }
  const updateStudentArr = (obj)=>{
    setStudentArr([...studentArr,obj])
  }
  return (
    <div>
      <AppSidebar  location={location} updateLocation={updateLocation} studentArr={studentArr} updateStudentArr={updateStudentArr} schoolArr={schoolArr} updateSchoolArr={updateSchoolArr} />
      <div className="wrapper d-flex flex-column min-vh-100 bg-light">
        <AppHeader />
        <div className="body flex-grow-1 px-3">
          <AppContent  location={location} updateLocation={updateLocation} studentArr={studentArr} updateStudentArr={updateStudentArr} schoolArr={schoolArr} updateSchoolArr={updateSchoolArr} />
        </div>
      </div>
    </div>
  )
}

export default DefaultLayout
